body {
  font-family: 'Lato', sans-serif;
  background-color: #f3f3f3;
  padding: 0;
  margin: 0; }

.d-none {
  display: none; }

.container {
  margin-left: auto;
  margin-right: auto;
  width: 80%; }

.header-background {
  background: #3a2820 url(./img/background1.jpg)center no-repeat fixed;
  -moz-background-size: 100%;
  -webkit-background-size: 100%;
  o-background-size: 100%;
  background-size: 100%;
  height: 100vh;
  margin-bottom: 30px; }

h1 {
  font-weight: bold;
  font-size: 8vh;
  padding-top: 40vh;
  padding-left: 9vh;
  margin: 0;
  color: #fff; }

.article {
  width: 100%;
  background-color: #fff;
  margin: 25px 0px;
  padding: 5% 10%;
  padding-bottom: 35px;
  border: 1px solid rgba(0,0,0,.125);
  transition: 0.4s ease;
  &-manage {
    float: right;
    margin-right: -40px;
    margin-top: -25px; }
  &-remove {
    display: inline;
    color: #000;
    font-size: 13px;
    border-radius: 50px;
    outline: none;
    padding: 2px 6px;
    font-weight: bold;
    transition: 0.5s ease;
    margin-left: 5px;
    &:hover {
      outline: none;
      transition: 0.5s ease;
      text-decoration: underline; } }
  &-title {
    font-weight: bold; }
  .read-more {
    outline: none;
    font-weight: bold;
    float: right;
    background-color: #000;
    color: #fff;
    border-radius: 50px;
    border: none;
    padding: 5px 12px;
    cursor: pointer;
    margin-top: 10px;
    margin-right: 50px; }
  &-description {
    font-size: 20px; }
  &-text {
    transition: 0.5s ease;
    display: none;
    &-open {
      display: block; } } }

.comments-wrapper {
  margin-top: 50px;
  margin-left: 20px;
  .comments-title {
    font-weight: bold;
    font-size: 23px;
    margin-bottom: 30px; }
  .comment {
    margin: 10px;
    padding: 10px 10px;
    border: 1px solid #bfbfbf;
    &-add-title {
      font-size: 25px;
      margin-bottom: 25px; }
    &-new {
      width: 150%;
      margin-left: 10px;
      margin-top: 30px;
      .comment-add-author {
        &-title {
          font-weight: bold;
          display: inline;
          font-size: 15px;
          color: #0371ac;
          margin-left: 20px; }
        &-input {
          padding: 0px 5px; } } }
    &-text-area {
      margin-top: 20px;
      margin-bottom: 5px;
      padding: 2px 5px;
      width: 80%;
      height: 200px;
      &-red {
        border-color: red; } }
    &-autor {
      color: #0371ac; }
    &-text {
      font-size: 14px; }
    &-remove {
      display: block;
      margin-left: auto;
      margin-right: 10px;
      color: #000;
      font-size: 13px;
      border-radius: 50px;
      outline: none;
      padding: 2px 6px;
      font-weight: bold;
      transition: 0.5s ease;
      &:hover {
        transition: 0.5s ease;
        text-decoration: underline; } }
    &-new-button {
      outline: none;
      font-size: 16px;
      color: #000;
      padding: 8px 17px;
      border: 1px solid black;
      margin-left: 10px;
      font-weight: bold;
      transition: all 0.5s ease;
      &-sub {
        margin-left: 0; }
      &:hover {
        background-color: #000;
        color: #fff; } }
    &-add-author {
      &-title {
        margin-left: auto; } } } }

.comment-down {
  margin-top: 40px;
  .count-comments {
    margin-top: 17px;
    font-weight: bold;
    font-size: 16px; } }

.new-article {
  width: 100%;
  margin-bottom: 50px;
  margin-top: 20px;
  outline: none;
  &-create {
    &-btn {
      margin-left: auto;
      margin-right: auto;
      font-size: 19px;
      border: 2px solid #000;
      padding: 7px 10px;
      font-weight: bold;
      transition: 0.5s ease;
      outline: none;
      &:hover {
        outline: none;
        background-color: #000000;
        color: #fff; } }
    .create-article-title {
      font-size: 30px;
      margin-top: 20px;
      &-main {
        text-align: center;
        font-size: 35px; } }
    .create-article-input {
      &-title {
        padding: 10px;
        width: 100%;
        padding: 5px 10px;
        height: 40px; }
      &-description {
        padding: 10px;
        width: 100%;
        padding: 5px 10px;
        height: 140px; }
      &-text {
        padding: 10px;
        width: 100%;
        padding: 5px 10px;
        height: 500px; } }
    .create-article-button {
      margin-top: 12px;
      font-size: 23px;
      border: 2px solid #000;
      outline: none;
      padding: 5px 10px;
      transition: 0.5s ease;
      font-weight: bold;
      &:hover {
        background-color: #000000;
        color: #fff; } }
    .input-red {
      border: 1px solid red; } } }

// btn
$bg: #f3f8fa;
$white: #fff;
$black: #282936;

@mixin transition($property: all, $duration: 0.45s, $ease: cubic-bezier(0.65,0,.076,1)) {
  transition: $property $duration $ease; }

button {
  position: relative;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  padding: 0;
  font-size: inherit;
  font-family: inherit;
  display: block;
  margin-left: auto;
  font-size: 12.5px;
  &.learn-more {
    width: 12rem;
    height: auto;
    outline: none;
    .circle {
      @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
      outline: none;
      position: relative;
      margin: 0;
      width: 3rem;
      height: 3rem;
      background: $black;
      border-radius: 1.625rem;
      .icon {
        @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto;
        background: $white;
        &.arrow {
          @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
          left: 0.625rem;
          width: 1.125rem;
          height: 0.125rem;
          background: none;
          &::before {
            position: absolute;
            content: '';
            top: -0.25rem;
            right: 0.0625rem;
            width: 0.625rem;
            height: 0.625rem;
            border-top: 0.125rem solid #fff;
            border-right: 0.125rem solid #fff;
            transform: rotate(45deg); } } } }

    .button-text {
      @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0.75rem 0;
      margin: 0 0 0 1.85rem;
      color: $black;
      font-weight: 700;
      line-height: 25px;
      text-align: center;
      text-transform: uppercase; } }

  &:hover {
    .circle {
      width: 100%;
      .icon {
        &.arrow {
          background: $white;
          transform: translate(1rem, 0); } } }

    .button-text {
      color: $white; } } }
