@media (max-width: 1200px) {
  body {
    .header-background {
      background: #3a2820 url(./img/background1.jpg)center no-repeat fixed;
      width: 100%; } } }

@media (max-width: 992px) {
  .comments-wrapper {
    margin-left: 0;
    .comment-text-area {
      width: 100%; }
    .comment-new {
      width: 100%; } } }

// @media (max-width: 768px)
@media (max-width: 576px) {
  .container {
    width: 90%; }
  h1 {
    padding-left: 20px;
    font-size: 50px; }
  .article {
    padding-top: 60px;
    .article-manage {
      margin-right: 0;
      margin-top: 0;
      margin-top: -45px; } }

  .comments-wrapper {
    margin-left: 0; }

  .count-comments {
    text-align: center; }
  .learn-more {
    margin-top: 30px;
    margin-right: -30px; } }
